<script>
  import Region from '@/components/Common/StoreCommon/Region'
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  import * as qiniu from 'qiniu-js'

  import constant from "./constant"
  import ShowStore from "./components/ShowStore"
  import Banner from "./components/Banner"
  import Icon from "./components/Icon"
  import NewUser from "./components/NewUser"
  import Timely from "./components/Timely"
  import GroupBuy from "./components/GroupBuy"
  import Floor from "./components/Floor"
  import Presale from "./components/Presale"
  import ChooseGoods from "./components/ChooseGoods"
  import ChooseGoodsForFloor from "./components/ChooseGoodsForFloor"
  import ChooseStore from "./components/ChooseStore"
  const bannerUrlList = {
    getQiNiuToken: '/foundation/qiniu/auth/file'
  }
  const setPageMgtUrl = {
    // 获取模板组件元素
    findTempAndComp: '/plateform/a/mall/pageTemp/findTempAndComp',
    // 保存/更新模板和组件元素数据
    saveOrUpdatePageTemp: '/plateform/a/mall/pageTemp/saveOrUpdatePageTemp',
    // 获取三方平台店内分类
    getCategorys: '/plateform/a/category/getCategorys',
    // 选择专题
    selectPageTopicList: '/plateform/a/mall/pageTemp/selectPageTopicList',
    // 获取模板组件元素
    findCompElement: '/plateform/a/mall/pageTemp/findCompElement',
    // 模板上下架
    upOrLowerPage: '/plateform/a/mall/pageTemp/upOrLowerPage',
    // 获取虚拟门店
    getStoreVirtualList: '/plateform/a/store/condition/getStoreVirtualList'

  }
  export default {
    data() {
      return {
        regionCode: Storage.get('regionCode'), // 广州市区域走不同的选择门店类型
        chooseStoreType: 1, // 选择门店类型
        loading: false,
        pageType: '', // 页面类型 1 首页  2 模板
        useStatus: 2, // 上下架状态
        useStatusStr: '下架中', // 下架中
        templateId: '', // 模板ID

        pageName: '', // 页面名
        pageId: '', // 页面ID(新增为空，编辑时必选)

        layoutMesgList: [], // 页面模板
        compListArrObj: {}, // 页面模板数据

        bannerLayoutList: [], // 轮播图布局
        iconLayoutList: [], // icon列表布局
        floorLayoutList: [], // 楼层列表布局

        // 当前点击的模板类型，控制添加内容显示用
        compType: '',
        compSortNum: '',

        // 跳转类型选择
        jumpTypeOptions: constant.jumpTypeOptions,
        // banner 索引 来控制上传的是哪一张图片
        upImgElemSortNum: 'imgES-1',
        upImgCompSortNum: 'imgCS-1',
        imageUploadLoading: false,
        // 保存防止多次点击
        isDisabled: false,

        categoryList: [], // 分类集合
        // 分类id
        categoryId: "",
        // 分类弹框是否显示
        catDialogFormVisible: false,
        // 选择分类发生变化使用 用compTypeTemp和elemSortNumTemp确定用户当前点击的是哪一个选择跳转路径的跳转框
        compTypeTemp: '',
        // 选择分类发生变化使用 用compTypeTemp和elemSortNumTemp确定用户当前点击的是哪一个选择跳转路径的跳转框
        elemSortNumTemp: '',

        // 专题弹窗是否显示
        spTopDialogFormVisible: false,
        // 专题集合
        spTopDialogOptions: [],
        // 专题ID
        spTopRelationId: '',

        // 商品详情弹框是否展示
        goodsDetailDialogFormVisible: false,

        // 活动
        activityTypeOptions: constant.getActivityTypeOptions(11),
        // 活动 - 秒杀
        activityTypeOptionsTimely: constant.getActivityTypeOptions(21),
        // 活动 - 新人专享
        activityTypeOptionsNewUser: constant.getActivityTypeOptions(41),
        // 活动 - 爆款
        activityTypeOptionsBK: constant.getActivityTypeOptions(61),
        // 活动 - 预售
        activityTypeOptionsPresale: constant.getActivityTypeOptions(51),
        // 门店选择
        storeCheckedList: [],
        showSelectStore: false,
        appIdOptions: constant.appIdOptions,
        virtualStoreList: [],
        virtualStoreNo: ''
      }
    },
    created() {
      this.pageType = this.$route.params.pageType
      this.templateId = this.$route.params.tempId ? parseInt(this.$route.params.tempId) : ''
    },
    mounted() {
      // 获取模板组件元素
      this.findTempAndComp()
      // 三方平台店内分类
      this.getCategoryId()
      // 获取要选择的专题 
      this.selectPageTopicList()
      if(this.regionCode == 42216) {
        // 广州市  获取虚拟门店
        this.getStoreVirtualList()
      }
    },
    components: {
      "choose-goods": ChooseGoods,
      "choose-goods-for-floor": ChooseGoodsForFloor,
      "choose-store": ChooseStore,
      "show-store": ShowStore,
      "banner": Banner,
      "icon": Icon,
      "new-user": NewUser,
      "timely": Timely,
      "group-buy": GroupBuy,
      "floor": Floor,
      "presale": Presale,
      "region": Region
    },
    methods: {
      /**
       * [doCityChangeHandel 城市发生变化]
       * @return {[type]} [description]
       */
      doCityChangeHandel(_val) {
        if(_val == "") {
          this.spTopDialogOptions = []
        }
        this.cityId = _val
        this.pageName = ''
        // 重置关联ID
        this.spTopRelationId = ''
        // 重置选中的组件和组件在当前页面的序号
        this.compType = ''
        this.compSortNum = ''
        // 获取模板组件元素
        this.findTempAndComp()
        // 选择专题
        this.selectPageTopicList()
        if(this.regionCode == 42216) {
          // 广州市  获取虚拟门店
          this.getStoreVirtualList()
        }
      },
      /**
       * [findTempAndComp 获取模板组件元素]
       * @return {[type]} [description]
       */
      findTempAndComp() {
        this.loading = true
        this.$http.post(setPageMgtUrl.findTempAndComp, {tempId: this.templateId, DIFFCITY: true}).then(res => {
          this.loading = false
          if(res.code === 0) {
            if(res.data) {
              this.layoutMesgList = res.data
              this.layoutMesgList.forEach(item => {
                item.templateId = this.templateId
                this.getLayoutDetail(item)
              })
            }else {
              this.layoutMesgList = []
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示(获取模板组件元素)',
            message: e.message
          })
        })
      },
      /**
       * [getLayoutDetail 根据ID获取详情 组装数据]
       * @return {[type]} [description]
       */
      getLayoutDetail({componentId, templateId, compSortNum, compType}) {
        this.$http.post(setPageMgtUrl.findCompElement, {componentId: componentId, templateId: templateId, compSortNum: compSortNum, DIFFCITY: true}).then(res => {
          if(res.data && res.data.length) {
            for(let item of res.data) {
              item.compSortNum = compSortNum // 组件数据排序：数值越小，优先级越高
              item.compType = compType
              item.imgUrl = '' // 图片Url地址
              item.jumpType = 0 // 跳转类型
              item.relationName = '' // 跳转Url地址
              item.jumpUrl = '' // 跳转Url地址
              item.platformCityGoodsIdList = [] // 区域商品id集合
              item.mallPageGoodsVos = [] // 选中的商品集合
              // 专题模板 选择商品配置商品来源 默认来源为区域商品
              if(compType === 'GOODS') {
                item.activityType = 0
              }
              item.relationId = '' // 关联ID:区域商品ID,专题id,分类id
              item.skuNo = '' // 关联ID是区域商品ID：商品编号 必返回
              if(compType === 'ACTIVITY') {
                item.title = '拼着买，更便宜' // 标题
                item.desc = '邀好友一起购' // 描述
                item.activityType = 11 // 活动类型
              }else if(compType === 'SECKILLACTIVITY') {
                item.title = '限时秒杀' // 标题
                item.activityType = 21 // 活动类型
              }else if(compType === 'REGISTER_ACTIVITY') {
                item.title = '新人专享专区' // 标题
                item.activityType = 41 // 活动类型
              }else if(compType === 'BEST_SELLER') {
                item.title = '每日爆款' // 标题
                item.activityType = 61 // 活动类型
              }else if(compType === 'ADVANCE') {
                item.title = '为您优选' // 标题
                item.activityType = 51 // 活动类型
              }else {
                item.title = '' // 标题
              }
            }
            this.$set(this.compListArrObj, compSortNum, { layoutList: res.data})
          }
          // console.log("this.compListArrObj:", this.compListArrObj)
        }).catch(e => {})
      },
      /**
       * [clickTemplateLayout 点击不同的layout触发事件]
       * @return {[type]} [description]
       */
      clickTemplateLayout(_type, _compSortNum) {
        this.compType = _type
        this.compSortNum = _compSortNum
      },
      /**
       * [getToken 通过后台封装的接口获取七牛的token]
       * @return {[type]}            [description]
       */
      getToken () {
        return this.$http.post(bannerUrlList.getQiNiuToken, {}).then(res => {
          if (res.code == 0) {
            let token = res.data.token
            let fileName = res.data.key
            return {fileName, token}
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.imageUploadLoading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [imageUploadReg 上传图片检测]
       * @return {[type]}      [description]
       */
      imageUploadReg (file) {
        const isPic = file.type.indexOf('image/') !== -1
        const isJPG = file.type === 'image/jpeg' || 'image/png'
        if (!isPic) {
          this.$message.error('图片文件必须是图片格式!')
        }
        if (!isJPG) {
          this.$message.error('图片只能是jpg或png格式!')
        }
        /**
         * [检测上传图片宽高]
         * @param  {[type]}   resolve [description]
         * @param  {Function} reject) {                     let width [description]
         * @param  {[type]}   ()      [description]
         * @return {[type]}           [description]
         */
        const isSize = new Promise(function(resolve, reject) {
          let width = 750
          let height = 280  

          let _URL = window.URL || window.webkitURL
          let img = new Image();
          img.onload = function() {
            let valid = img.width == width && img.height == height
            valid ? resolve() : reject()
          }
          img.src = _URL.createObjectURL(file)
        }).then(() => {
          return file
        }, () => {
          this.$message.error('请上传750*280像素的jpg或png图片!')
          return Promise.reject()
        })

        return isPic && isJPG && isSize
      },
      /**
       * [iconImageUploadReg 上传图片检测]
       * @return {[type]}      [description]
       */
      iconImageUploadReg (file) {
        const isPic = file.type.indexOf('image/') !== -1
        const isJPG = file.type === 'image/jpeg' || 'image/png'
        if (!isPic) {
          this.$message.error('图片文件必须是图片格式!')
        }
        if (!isJPG) {
          this.$message.error('图片只能是jpg或png格式!')
        }
        /**
         * [检测上传图片宽高]
         * @param  {[type]}   resolve [description]
         * @param  {Function} reject) {                     let width [description]
         * @param  {[type]}   ()      [description]
         * @return {[type]}           [description]
         */
        const isSize = new Promise(function(resolve, reject) {
          let width = 120
          let height = 120   

          let _URL = window.URL || window.webkitURL
          let img = new Image();
          img.onload = function() {
            let valid = img.width == width && img.height == height
            valid ? resolve() : reject()
          }
          img.src = _URL.createObjectURL(file)
        }).then(() => {
          return file
        }, () => {
          this.$message.error('请上传120*120像素的jpg或png图片!')
          return Promise.reject()
        })

        return isPic && isJPG && isSize
      },
      /**
       * [topImageUploadReg 上传图片检测]
       * @return {[type]}      [description]
       */
      topImageUploadReg (file) {
        const isPic = file.type.indexOf('image/') !== -1
        const isJPG = file.type === 'image/jpeg' || 'image/png'
        if (!isPic) {
          this.$message.error('图片文件必须是图片格式!')
        }
        if (!isJPG) {
          this.$message.error('图片只能是jpg或png格式!')
        }
        /**
         * [检测上传图片宽高]
         * @param  {[type]}   resolve [description]
         * @param  {Function} reject) {                     let width [description]
         * @param  {[type]}   ()      [description]
         * @return {[type]}           [description]
         */
        const isSize = new Promise(function(resolve, reject) {
          let width = 750
          let height = 750   

          let _URL = window.URL || window.webkitURL
          let img = new Image();
          img.onload = function() {
            let valid = img.width == width && img.height == height
            valid ? resolve() : reject()
          }
          img.src = _URL.createObjectURL(file)
        }).then(() => {
          return file
        }, () => {
          this.$message.error('请上传750*750像素的jpg或png图片!')
          return Promise.reject()
        })

        return isPic && isJPG && isSize
      },
      /**
       * [floorImageUploadReg 楼层图片上传检测]
       * @return {[type]} [description]
       */
      floorImageUploadReg (file) {
        const isPic = file.type.indexOf('image/') !== -1
        const isJPG = file.type === 'image/jpeg' || 'image/png'
        if (!isPic) {
          this.$message.error('图片文件必须是图片格式!')
        }
        if (!isJPG) {
          this.$message.error('图片只能是jpg或png格式!')
        }
        /**
         * [检测上传图片宽高]
         * @param  {[type]}   resolve [description]
         * @param  {Function} reject) {                     let width [description]
         * @param  {[type]}   ()      [description]
         * @return {[type]}           [description]
         */
        const isSize = new Promise(function(resolve, reject) {
          let width = 690
          let height = 200

          let _URL = window.URL || window.webkitURL
          let img = new Image();
          img.onload = function() {
            let valid = img.width == width && img.height == height
            valid ? resolve() : reject()
          }
          img.src = _URL.createObjectURL(file)
        }).then(() => {
          return file
        }, () => {
          this.$message.error('请上传690*200像素的jpg或png图片!')
          return Promise.reject()
        })

        return isPic && isJPG && isSize
      },
      /**
       * [emitUpload 上传图片]
       * @return {[type]}   [description]
       */
      emitUpload (e) {
        this.imageUploadLoading = true
        this.getToken().then((res) => {
          // 开始上传图片
          let uploader = qiniu.upload(e.file, res.fileName, res.token)
          uploader.subscribe({
            error: () => {
              this.imageUploadLoading = false
            },
            complete: (res) => {
              let urlImage = 'http://retailimg01.benlailife.com/' + res.key
              this.imageUploadLoading = false

              this.setImgUrl(urlImage)
            },
            next: res => {
            }
          })
        })
      },
      /**
       * [setImgUrl 设置图片地址]
       * @param {[type]} _imgUrl [description]
       */
      setImgUrl(_imgUrl) {
        for(let item of this.compListArrObj[this.upImgCompSortNum]['layoutList']) {
          if(item.elemSortNum == this.upImgElemSortNum) {
            item.imgUrl = _imgUrl
          }
        }
      },
      /**
       * [setUpImgIndex 设置索引]
       * @param {[type]} _index [description]
       */
      setUpImgIndex(_elemSortNum, _compSortNum) {
        if(this.imageUploadLoading) {
          return
        }
        this.upImgElemSortNum = _elemSortNum
        this.upImgCompSortNum = _compSortNum
      },
      /**
       * [deleteAddIconItem 删除]
       * @return {[type]} [description]
       */
      deleteAddMesg(_elemSortNum, _compSortNum) {
        for(let item of this.compListArrObj[_compSortNum]['layoutList']) {
          if(item.elemSortNum == _elemSortNum) {
            item.imgUrl = ""
            item.title = ""
            item.jumpType = 0

            item.skuNo = ''
            item.relationId = ''
            item.jumpUrl = ''
            item.relationName = ''

            item.platformCityGoodsIdList = [] // 区域商品id集合
            item.mallPageGoodsVos = [] // 选中的商品集合
          }
        }
      },
      /**
       * [doSaveHandel 保存并退出   1  保存并继续    2  保存并退出 ]
       * @return {[type]} [description]
       */
      doSaveHandel(_handelType, _userStatus) {
        let handelType = _handelType
        let userStatus = _userStatus

        let storeNoListArr = []
        if(this.regionCode == 42216 && this.chooseStoreType === 2) {
          storeNoListArr.push(this.virtualStoreNo)
        }else {
          if(!this.storeCheckedList || this.storeCheckedList.length === 0) {
            this.$message("请选择应用门店")
            return
          }
          storeNoListArr = this.storeCheckedList.map(item => {
            return item.storeNo
          })
        }

        if(!this.pageName) {
          this.$message("请输入页面名称")
          return
        }else {
          if(this.pageName.length > 30) {
            this.$message("页面名称限制30个字")
            return
          }
        }
        let pageId = this.pageId
        let pageName = this.pageName
        let pageType = this.pageType
        let templateId = this.templateId

        let layoutListArr = []
        for(let item of this.layoutMesgList) {
          if(this.compListArrObj[item.compSortNum]) {
            layoutListArr = [...layoutListArr, ...this.compListArrObj[item.compSortNum]['layoutList']]
          }else {
            layoutListArr = [...layoutListArr]
          }
        }
        for(let item of layoutListArr) {
          item.platformCityGoodsIdList = []
          if(item.jumpType === 6) {
            item.relationId = `${item.appId}#${item.appPath}`
          }
          if(item.mallPageGoodsVos && item.mallPageGoodsVos.length) {
            for(let j of item.mallPageGoodsVos) {
              // 专题取的是预售商品  应该传区域活动平台商品id，首页取区域商品，传区域平台商品id
              if(item.activityType === 51) {
                item.platformCityGoodsIdList.push(j.activityPlatCityGoodsId)
              }else {
                item.platformCityGoodsIdList.push(j.platformCityGoodsId)
              }
            }
          }
        }
        
        let _addParams = {
          DIFFCITY: true,
          regionCode: this.regionCode,
          pageName: pageName,
          pageType: pageType,
          pageId: pageId,
          templateId: templateId,
          elementList: layoutListArr,
          storeNoList: storeNoListArr
        }

        this.isDisabled = true
        this.$http.post(setPageMgtUrl.saveOrUpdatePageTemp, _addParams).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            if(res.data) {
              this.pageId = res.data.pageId
            }
            if(handelType === 1) {
              // 保存并继续
              this.$message.success("保存成功")
            }else if(handelType === 2) {
              // 保存并退出
              this.$message.success("保存成功")
              this.$router.push('/set_page')
            }else if(handelType === 3) {
              // 执行上架操作
              this.upOrLowerPage(userStatus)
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [doCancelHandel 取消]
       * @return {[type]} [description]
       */
      doCancelHandel() {
        this.$confirm('确定取消吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$router.push('/set_page')
        }).catch(() => {})
      },
      /**
       * [getCategoryId 三方平台店内分类]
       * @return {[type]} [description]
       */
      getCategoryId() {
        this.$http.post(setPageMgtUrl.getCategorys, {DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              for(let item of res.data) {
                if(item.categoryVoList && item.categoryVoList.length === 0) {
                  item.categoryVoList = null
                }
              }
              this.categoryList = res.data
            }else {
              this.categoryList = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(三方平台店内分类):", e.message)
        })
      },
      /**
       * [selectPageTopicList 选择专题]
       * @return {[type]} [description]
       */
      selectPageTopicList() {
        // 模板数据类型：1-首页；2-专题页
        this.$http.post(setPageMgtUrl.selectPageTopicList, {pageType: 2, DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              this.spTopDialogOptions = res.data
            }else {
              this.spTopDialogOptions = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(选择专题):", e.message)
        })
      },
      /**
       * [changeJumpType 修改跳转类型]
       * @return {[type]} [description]
       */
      changeJumpType(_val) {
        if(_val === 1) {
          // 专题
          this.spTopRelationId = ""
          this.spTopDialogFormVisible = true
        }else if(_val === 2) {
          // 分类
          this.categoryId = ""
          this.catDialogFormVisible = true
        }else if(_val === 3) {
          // 预售商品详情
          let _params = {
            title: '选择商品',
            regionCode: this.regionCode,
            // 获取商品区分(null,0:正常及时达,1:预售,2:秒杀,3:爆款)
            isAdvance: 1
          }
          this.$refs.chooseGoods.setDefault(_params)
        }else if(_val === 7) {
          // 秒杀商品详情
          let _params = {
            title: '选择商品',
            regionCode: this.regionCode,
            isAdvance: 2
          }
          this.$refs.chooseGoods.setDefault(_params)
        }else if(_val === 8) {
          // 爆款商品详情
          let _params = {
            title: '选择商品',
            regionCode: this.regionCode,
            isAdvance: 3
          }
          this.$refs.chooseGoods.setDefault(_params)
        }
        for(let item of this.compListArrObj[this.upImgCompSortNum]['layoutList']) {
          if(item.elemSortNum == this.upImgElemSortNum) {
            item.relationName = ''
            item.relationId = ''
          }
        }
      },
      /**
       * [changeJumpTypeByBtn 点击修改按钮修改]
       * @param  {[type]} _elemSortNum [description]
       * @param  {[type]} _compSortNum [description]
       * @param  {[type]} _jumpType    [description]
       * @return {[type]}              [description]
       */
      changeJumpTypeByBtn(_elemSortNum, _compSortNum, _jumpType) {
        // 设置索引
        let elemSortNum = _elemSortNum
        let compSortNum = _compSortNum
        this.upImgElemSortNum = elemSortNum
        this.upImgCompSortNum = compSortNum

        let _val = _jumpType
        if(_val === 1) {
          // 专题
          this.spTopRelationId = ""
          this.spTopDialogFormVisible = true
        }else if(_val === 2) {
          // 分类
          this.categoryId = ""
          this.catDialogFormVisible = true
        }else if(_val === 3) {
          // 预售商品详情
          let _params = {
            title: '选择商品',
            regionCode: this.regionCode,
            // 获取商品区分(null,0:正常及时达,1:预售,2:秒杀,3:爆款)
            isAdvance: 1
          }
          this.$refs.chooseGoods.setDefault(_params)
        }else if(_val === 7) {
          // 秒杀商品详情
          let _params = {
            title: '选择商品',
            regionCode: this.regionCode,
            isAdvance: 2
          }
          this.$refs.chooseGoods.setDefault(_params)
        }else if(_val === 8) {
          // 爆款商品详情
          let _params = {
            title: '选择商品',
            regionCode: this.regionCode,
            isAdvance: 3
          }
          this.$refs.chooseGoods.setDefault(_params)
        }
      },
      /**
       * [doConfirmHandel 弹框点击确定选择]
       * @return {[type]} [description]
       */
      doConfirmHandel(_val) {
        // console.log("_val:", _val)
        // console.log("this.upImgCompSortNum:", this.upImgCompSortNum)
        // console.log("this.upImgElemSortNum:", this.upImgElemSortNum)
        let activityPlatCityGoodsId = _val.activityPlatCityGoodsId || ''
        let activityType = _val.activityType
        let skuNo = _val.skuNo || ''
        let goodsName = _val.goodsName || ''
        for(let item of this.compListArrObj[this.upImgCompSortNum]['layoutList']) {
          if(item.elemSortNum == this.upImgElemSortNum) {
            if(item.jumpType == 1) {
              // 专题
              item.relationId = this.spTopRelationId
              let spItemMesg = this.spTopDialogOptions.filter(i => {
                return i.pageId === this.spTopRelationId
              })[0]
              item.skuNo = ''
              if(spItemMesg) {
                item.relationName = spItemMesg.pageName
              }
              this.spTopDialogFormVisible = false
            }else if(item.jumpType == 2) {
              // 分类
              item.relationId = this.categoryId
              let categotyItemMesg = this.categoryList.filter(i => {
                return i.categoryId === this.categoryId
              })[0]
              item.skuNo = ''
              if(categotyItemMesg) {
                item.relationName = categotyItemMesg.categoryName
              }
              this.catDialogFormVisible = false 
            }else if(item.jumpType == 3 || item.jumpType == 7 || item.jumpType == 8) {
              // 商品详情
              item.relationId = activityPlatCityGoodsId
              item.skuNo = skuNo
              item.relationName = goodsName
              item.activityType = activityType
            }else {
              item.relationId = ''
              item.skuNo = ''
              item.relationName = ''
            }
          }
        }
      },
      /**
       * [getAddGoodsDialog 点击添加商品]
       * @return {[type]} [description]
       */
      getAddGoodsDialog(_elemSortNum, _compSortNum, _mallPageGoodsVos, activityType) {
        this.upImgElemSortNum = _elemSortNum
        this.upImgCompSortNum = _compSortNum
        let mallPageGoodsVos = _mallPageGoodsVos
        let _params = {
          title: '选择商品',
          checkedList: mallPageGoodsVos,
          regionCode: this.regionCode,
          templateId: this.templateId,
          activityType: activityType || 0
        }
        this.$refs.chooseGoodsForFloor.setDefault(_params)
      },
      /**
       * [confirmChooseGoodsForFloor 设置选中的商品]
       * @return {[type]} [description]
       */
      confirmChooseGoodsForFloor(_val) {
        let checkedList = []
        if(_val && _val.checkedList) {
          checkedList = _val.checkedList
        }

        for(let item of this.compListArrObj[this.upImgCompSortNum]['layoutList']) {
          if(item.elemSortNum == this.upImgElemSortNum) {
            item.mallPageGoodsVos = checkedList
          }
        }
      },
      /**
       * [deletGoodsItem 删除插入的商品]
       * @param  {[type]} _elemSortNum [description]
       * @param  {[type]} _compSortNum [description]
       * @return {[type]}              [description]
       */
      deletGoodsItem(elemSortNum, compSortNum, index) {
        for(let item of this.compListArrObj[compSortNum]['layoutList']) {
          let mallPageGoodsVosArr = item.mallPageGoodsVos
          if(item.elemSortNum === elemSortNum) {
            item.mallPageGoodsVos.splice(index, 1)
          }
        }
      },
      /**
       * [upOrLowerPage 上架操作]
       * @return {[type]} [description]
       */
      upOrLowerPage(_optType) {
        // pageType 1 首页   2 专题页    useStatus  1 上架中   2  下架中
        // optType up 上架  down下架
        let optType = _optType
        let optionTip = ''
        let optionType = ''
        let userStatus = ''
        if(this.pageType == 1) {
          optionTip = '您确定要上架当前页面，替换原首页吗？'
          optionType = '上架操作确认'
        }else if(this.pageType == 2) {
          if(optType === 'up') {
            optionTip = '您确定要上架当前专题吗？'
            optionType = '上架操作确认'
          }else if(optType === 'down') {
            optionTip = '您确定要下架当前专题吗？'
            optionType = '下架操作确认'
          }
        }
        if(optType === 'up') {
          userStatus = 1
        }else if(optType === 'down') {
          userStatus = 2
        }
        
        this.$confirm(optionTip, optionType, {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.isDisabled = true
          this.$http.post(setPageMgtUrl.upOrLowerPage, {pageId: this.pageId, pageType: this.pageType, userStatus: userStatus, DIFFCITY: true}).then(res => {
            setTimeout(() => {
              this.isDisabled = false
            }, 800)
            if(res.code === 0) {
              this.$message.success("操作成功")
              this.$router.push('/set_page')
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.isDisabled = false
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * @description: 选择门店
       * @param {type} 
       * @return: 
       */
      showStoreSelectDialog() {
        let _params = {
          title: '选择门店',
          checkedList: this.storeCheckedList,
          regionCode: this.regionCode
        }
        this.$refs.chooseStore.setDefault(_params)
      },
      confirmChooseStore(params) {
        this.storeCheckedList = params.checkedList && params.checkedList.length ? JSON.parse(JSON.stringify(params.checkedList)) : []
      },
      /**
       * @description: 修改状态
       * @param {type} 
       * @return: 
       */
      changeSelectStoreStatus(status) {
        this.showSelectStore = status
      },
      getStoreVirtualList() {
        this.$http.post(setPageMgtUrl.getStoreVirtualList, {DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            let virtualStoreListTemp = res.data && res.data.length ? res.data : []
            virtualStoreListTemp.forEach(item => {
              item.virtualStoreName = `${item.storeNo}_${item.storeName}`
            })
            this.virtualStoreNo = virtualStoreListTemp[0].storeNo
            this.virtualStoreList = virtualStoreListTemp
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取虚拟门店):", e.message)
        })
      }

    }

  }
</script>


