<template>
  <el-table
    class="fixed-height"
    :data="tableData"
    empty-text="没有查询到您想要的数据"
    style="width:460px;"
    max-height="270"
    size="small">
    <el-table-column min-width="170" label="门店编码/名称">
      <template slot-scope="scope">
        <div>
          {{scope.row.storeNo}}_{{scope.row.storeName}}
        </div>
      </template>
    </el-table-column>
    <el-table-column min-width="100" prop="cityName" label="城市"></el-table-column>
    <el-table-column min-width="100" prop="saleTypeStr" label="门店类型"></el-table-column>
  </el-table>
</template>
<script>
  export default {
    props: {
      tableData: {
        type: Array,
        default: []
      }
    },
    data() {
      return {}
    }
  }
</script>


