<style scoped>
.gb-active-wraper {
  margin-top: 6px;
  height: auto;
}
.sf-goods-msg-wrap {
  background-color: #ffffff;
}
.sf-goods-name {
  color: #666;
  font-size: 14px;
  line-height: 22px;
  padding: 0 10px;
  box-sizing: border-box;
}
.sg-goods-msg-two-col {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: nowrap;
  font-size: 12px;
  line-height: 20px;
  padding: 0 10px;
  box-sizing: border-box;
  align-items: center;
  color: #F98549;
  text-align: left;
}
.sf-goods-sale-num {
  color: #666;
  text-align: right;
}
.sf-goods-sale-num>span {
  color: red;
}
.sf-goods-add-cart-wrap {
  width: 80px;
  text-align: center;
  height: 30px;
  background-color: red;
  color: #FFF;
  border-radius: 15px;
  line-height: 30px;
  box-sizing: border-box;
}
.pb10 {
  padding-bottom: 10px;
}
.floor-banner-img-wrap {
  padding: 0 2px;
  text-align: center;
  box-sizing: border-box;
}
.ex-floor-banner-img {
  height: 86px;
  width: 266px;
}
</style>
<template>
  <div class="gb-active-wraper">
    <div class="sf-goods-msg-wrap">
      <!-- 商品图片 -->
      <div class="floor-banner-img-wrap">
        <img src="/shop/static/imgs/no_img.png" alt="" class='ex-floor-banner-img'>
      </div>
      <div class="sf-goods-name">
        果肉软嫩味甜的奶油草莓，咬一口果汁迸溅，齿颊尽留香甜 1份装 约250g
      </div>
      <hr class="s-slip">
      <div class="sg-goods-msg-two-col">
        <div class="sf-goods-sale-time">预售时间：1月12日</div>
        <div class="sf-goods-sale-num">今日已售<span>699</span>份</div>
      </div>
      <div class="sg-goods-msg-two-col">
        <div class="sf-goods-sale-time">提货时间：1月13日</div>
      </div>
      <div class="sg-goods-msg-two-col pb10">
        <div>
          <span style="color: red;">￥<span style="font-size: 18px;">7.98</span></span>
          <span style="color: #666; text-decoration:line-through">￥24.00</span>
        </div>
        <div class="sf-goods-add-cart-wrap">加入购物车</div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {}
    }
  }
</script>


