<style scoped>
  .gb-active-wraper {
    margin-top: 6px;
    height: auto;
  }
  .active-item-wraper {
    height: auto;
    background-color: #FFF;
    padding: 5px 0 10px 0;
  }
  .active-top-wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    padding: 0 8px;
    box-sizing: border-box;
  }
  .act-title-txt {
    line-height: 26px;
    font-size: 16px;
    font-weight: bold;
    text-align: left;
  }
  .show-more-txt {
    display: flex;
    align-items: center;
    font-size: 14px;
    text-align: right;
  }
  .act-goods-item-wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    padding: 0 8px;
    box-sizing: border-box;
  }
  .act-goods-item {
    margin-top: 6px;
  }
  .act-ex-goods-img {
    width: 80px;
    height: 80px;
  }
  .price-txt-wrap {
    font-size: 10px;
    color: #666;
    line-height: 16px;
  }
  .act-des-txt-wrap {
    font-size: 12px;
    color: #666;
    padding: 0 8px;
    box-sizing: border-box;
  }
</style>
<template>
  <div class="gb-active-wraper">
    <div v-for="(activityItem, index) in activityList" :key="index">
      <div class="active-item-wraper">
        <div class="active-top-wrap">
          <div class="act-title-txt">{{activityItem.title || '拼着买，更便宜'}}</div>
          <div class="show-more-txt">查看更多></div>
        </div>
        <div class="act-des-txt-wrap">{{activityItem.desc || '邀好友一起购'}}</div>
        <div class="act-goods-item-wrap">
          <div class="act-goods-item" v-for="(item, idx) in 3" :key="idx">
            <div>
              <img src="/shop/static/imgs/no-goods-img.png" alt="" class='act-ex-goods-img'>
            </div>
            <div class="price-txt-wrap">
              <span style="color: #FD6400;">￥0.00</span>
              <span style="color: #666; text-decoration:line-through">￥0.00</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: {
      activityList: {
        type: Array,
        default: []
      }
    },
    data() {
      return {}
    }
  }
</script>


