<style scoped>
  .banner-wraper {
    background-color: #ffffff;
    height: 106px;
    cursor: pointer;
  }
  .banner-img, .ex-banner-img {
    width: 270px;
    height: 104px;
  }
</style>
<template>
  <div class="banner-wraper">
    <el-carousel height="104px" arrow="never" indicator-position="none">
      <el-carousel-item v-for="(bannerEleItem, index) in bannerList" :key="index">
        <img :src="bannerEleItem.imgUrl" alt="" v-if="bannerEleItem.imgUrl" class='banner-img'>
        <img src="/shop/static/imgs/no_img.png" alt="" v-else class='ex-banner-img'>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>
<script>
  export default {
    props: {
      bannerList: {
        type: Array,
        default: []
      }
    },
    data() {
      return {}
    }
  }
</script>


