<style scoped>
  .floor-wraper {
    margin-top: 6px;
    background-color: #ffffff;
    height: auto;
    box-sizing: border-box;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .floor-banner-img-wrap {
    padding: 0 2px;
    text-align: center;
    box-sizing: border-box;
  }
  .floor-banner-img, .ex-floor-banner-img {
    height: 86px;
    width: 266px;
  }
  .goods-wraper {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .goods-item-wraper {
    width: 116px;
    height: auto;
    text-align: center;
  }
  .goods-img, .ex-goods-img {
    width: 100px;
    height: 100px;
  }
  .goods-title-txt-wrap {
    font-size: 14px;
    color: #222222;
    font-weight: bold;
    text-align: left;
    line-height: 20px;
    padding: 0 5px;
  }
  .spec-title-txt-wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    font-size: 10px;
    color: #666;
    padding: 0 5px;
    line-height: 16px;
    overflow: hidden;
  }
  .spec-desc-txt-wrap, .goods-title-txt-wrap {
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
  }
  .price-cart-wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    padding: 4px 0;
  }
  .price-txt-wrap {
    font-size: 10px;
    color: #666;
    line-height: 16px;
  }
  .cart-img-wrap {
    display: flex;
    align-items: center;
  }
  .ex-cart-img {
    width: 22px;
    height: 22px;
  }
  .goods-wraper {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .goods-item-wraper {
    width: 116px;
    height: auto;
    text-align: center;
  }
  .goods-img, .ex-goods-img {
    width: 100px;
    height: 100px;
  }
</style>
<template>
  <div class="floor-wraper">
    <div v-for="(floorElmItem, index) in floorList" :key="index">
      <!-- 楼层中第一楼是图片的 -->
      <div v-if="floorElmItem.elemType == 1" class="floor-banner-img-wrap">
        <img :src="floorElmItem.imgUrl" alt="" v-if="floorElmItem.imgUrl" class='floor-banner-img'>
        <img src="/shop/static/imgs/no_img.png" alt="" v-else class='ex-floor-banner-img'>
      </div>
      <!-- 楼层中是商品的 -->
      <div v-if="floorElmItem.elemType == 2 && floorElmItem.mallPageGoodsVos && floorElmItem.mallPageGoodsVos.length > 0" class="goods-wraper">
        <div class="goods-item-wraper" v-for="(goodsItem, index) in floorElmItem.mallPageGoodsVos" :key="index">
          <div>
            <img :src="goodsItem.imgUrl" alt="" v-if="goodsItem.imgUrl" class='goods-img'>
            <img src="/shop/static/imgs/no-goods-img.png" alt="" v-else class='ex-goods-img'>
          </div>
          <div class="goods-title-txt-wrap">
            {{goodsItem.goodsName || '商品名称商品名称商品名称'}}
          </div>
          <div class="spec-title-txt-wrap">
            <div class="spec-desc-txt-wrap">
              {{goodsItem.spec || '规格规格规格规格规格'}}
            </div>
          </div>
          <div class="price-cart-wrap">
            <div class="price-txt-wrap" v-if="goodsItem.saleType === 4">
              <span style="color: #FD6400;">￥{{goodsItem.price || '0.00'}}</span>
              <span style="color: #666; text-decoration:line-through">￥{{goodsItem.price || '0.00'}}</span>
            </div>
            <div class="price-txt-wrap" v-if="goodsItem.saleType === 3">
              <span style="color: #FD6400;">￥{{goodsItem.presalePrice || '0.00'}}</span>
              <span style="color: #666; text-decoration:line-through">￥{{goodsItem.price || '0.00'}}</span>
            </div>
            <div class="cart-img-wrap">
              <img src="/shop/static/imgs/cart.png" alt="" class='ex-cart-img'>
            </div>
          </div>
        </div>
      </div>
      <div v-else-if="floorElmItem.elemType == 2 && (!floorElmItem.platformCityGoodsList || floorElmItem.platformCityGoodsList.length == 0)" class="goods-wraper">
        <div class="goods-item-wraper">
          <div>
            <img src="/shop/static/imgs/no-goods-img.png" alt="" class='ex-goods-img'>
          </div>
          <div class="goods-title-txt-wrap">
            商品名称
          </div>
          <div class="spec-title-txt-wrap">
            <div class="spec-desc-txt-wrap">
              规格
            </div>
          </div>
          <div class="price-cart-wrap">
            <div class="price-txt-wrap">
              <span style="color: #FD6400;">￥0.00</span>
              <span style="color: #666; text-decoration:line-through">￥0.00</span>
            </div>
            <div class="cart-img-wrap">
              <img src="/shop/static/imgs/cart.png" alt="" class='ex-cart-img'>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: {
      floorList: {
        type: Array,
        default: []
      }
    },
    data() {
      return {}
    }
  }
</script>


