<style scoped>
  .gb-active-wraper {
    margin-top: 6px;
    height: auto;
  }
  .active-item-wraper {
    height: auto;
    background-color: #FFF;
    padding: 5px 0 10px 0;
  }
  .active-top-wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    padding: 0 8px;
  }
  .act-title-txt {
    line-height: 26px;
    font-size: 16px;
    font-weight: bold;
    text-align: left;
  }
  .show-more-txt {
    display: flex;
    align-items: center;
    font-size: 14px;
    text-align: right;
  }
  .act-goods-item-wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
  }
  .goods-item-wraper {
    width: 116px;
    height: auto;
    text-align: center;
    padding: 0 8px;
  }
  .act-des-txt-wrap {
    font-size: 12px;
    color: #666;
  }
  .ex-goods-img {
    width: 100px;
    height: 100px;
  }
  .goods-title-txt-wrap {
    font-size: 14px;
    color: #222222;
    font-weight: bold;
    text-align: left;
    line-height: 20px;
    padding: 0 5px;
  }
  .price-cart-wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    padding: 4px 0;
  }
  .price-txt-wrap {
    font-size: 10px;
    color: #666;
    line-height: 16px;
  }
  .cart-img-wrap {
    display: flex;
    align-items: center;
  }
  .ex-cart-img {
    width: 22px;
    height: 22px;
  }

</style>
<template>
  <div class="gb-active-wraper">
    <div v-for="(activityItem, index) in activityList" :key="index">
      <div class="active-item-wraper">
        <div class="active-top-wrap">
          <div class="act-title-txt">
            {{activityItem.title || '限时秒杀'}}
            <span>00</span>:<span>00</span>:<span>00</span>
          </div>
          <div class="show-more-txt">查看更多></div>
        </div>
        <div class="act-goods-item-wrap">
          <div class="goods-item-wraper" v-for="(item, idx) in 2" :key="idx">
            <div>
              <img src="/shop/static/imgs/no-goods-img.png" alt="" class='ex-goods-img'>
            </div>
            <div class="goods-title-txt-wrap">
              商品名称
            </div>
            <div class="price-cart-wrap">
              <div class="price-txt-wrap">
                <span style="color: #FD6400;">0.00</span>
                <span style="color: #666; text-decoration:line-through">0.00</span>
              </div>
              <div class="cart-img-wrap">
                <img src="/shop/static/imgs/cart.png" alt="" class='ex-cart-img'>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: {
      activityList: {
        type: Array,
        default: []
      }
    },
    data() {
      return {}
    }
  }
</script>


