<style scoped>
  .icon-wraper {
    margin-top: 6px;
    background-color: #ffffff;
    height: 90px;
    padding: 10px;
    box-sizing: border-box;
    cursor: pointer;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
  }
  .icon-img-wrap {
    text-align: center;
  }
  .icon-title-txt-wrap {
    text-align: center;
    font-size: 12px;
  }
  .icon-img, .ex-icon-img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }
</style>
<template>
  <div class="icon-wraper">
    <div class="icon-item-wrap" v-for="(iconEleItem, index) in iconList" :key="index">
      <div class="icon-img-wrap">
        <img :src="iconEleItem.imgUrl" alt="" v-if="iconEleItem.imgUrl" class='icon-img'>
        <img src="/shop/static/imgs/no_icon.png" alt="" v-else class='ex-icon-img'>
      </div>
      <div class="icon-title-txt-wrap">
        {{iconEleItem.title || 'icon标题'}}
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: {
      iconList: {
        type: Array,
        default: function() {
          return []
        }
      }
    },
    data() {
      return {}
    }
  }
</script>


