<style scoped>
  .nu-active-wraper {
    margin-top: 6px;
    height: auto;
  }
  .nu-act-list-wrap {
    background-color: #FFFFFF; 
    padding: 8px;
  }
  .nu-act-list-con-wrap {
    height: 70px; 
    border-radius: 35px; 
    border: 1px solid #ececec; 
    display: flex; 
    flex-wrap: nowrap; 
    flex-direction: row; 
    align-items: center;
    padding: 8px 20px;
  }
  .nu-right-wrap {
    width: 40px; 
    height: 70px;
    display: flex;
    align-items: center;
    margin-left: 10px;
  }
  .go-txt-wrap {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 1px solid #ececec;
    line-height: 40px; 
    text-align: center; 
    background-color: #ececec;
  }
</style>
<template>
  <div class="nu-active-wraper">
    <div class="nu-act-list-wrap" v-for="(activityItem, index) in activityList" :key="index">
      <div class="nu-act-list-con-wrap">
        <div class="nu-left-wrap" style="flex: 1;">
          <div style="font-size: 15px; color: #222222; font-weight: bold;">{{activityItem.title || '新人专享专区'}}</div>
          <div style="font-size: 12px; color: #666666;">欢迎来到本来鲜，注册vip会员有礼哦。</div>
        </div>
        <div class="nu-right-wrap">
          <div class="go-txt-wrap">GO</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: {
      activityList: {
        type: Array,
        default: []
      }
    },
    data() {
      return {}
    }
  }
</script>


